/*------ most searched end -------*/


/*------ newest start -------*/
.newest {
    .dynamic-tabs {
        margin: 10px 0 0 0;
        .tabs {
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            li {
                background-color: #ebebeb;
                width: 197px;
                height: 42px;
                margin: 0 0 0 3px;
                transition: all .3s ease-in-out;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: ar-reg;
                font-size: 16px;
                color: #959595;
            }
            .active {
                background-color: #fff;
                color: #231f20;
                font-family: ar-bold;
            }
        }
        .content {
            background-color: #fff;
            padding: 28px 30px;
            > div {
                display: none;
            }
            .card {
                margin: 0 0 21px 18px;
            }
            .card:last-child {
                margin: 0 0 21px 0;
            }
            .active {
                display: flex !important;
                justify-content: flex-start;
                align-items: center;
                flex-wrap: wrap;
            }
            .show-more {
                display: block;
                border: none;
                border-radius: 2px;
                background-color: #f7f7f7;
                outline: none;
                width: 118px;
                height: 34px;
                margin: 0 auto 6px auto;
                font-family: ar-reg;
                font-size: 14px;
                color: #b3b3b3;
                transition: all .5s ease-in-out;
                &:hover {
                    text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
                    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
                    border-radius: 50px;
                    color: #231f20;
                }
            }
        }
    }
}
/*------ newest end -------*/



/*------ footer start -------*/
.footer {
    margin-top: 10px;
    padding-top: 70px;
    background-color: #221e1f;
    .slide {
        .list-header {
            font-family: ar-bold;
            font-size: 16px;
            color: #ffffff;
            margin: 0 0 26px 0;
            padding: 0;
        }
        ul {
            margin: 0;
            padding: 0 0 70px 0;
            li {
                margin: 0 0 9px 0;
                a {
                    font-family: ar-reg;
                    font-size: 14px;
                    color: #ababab;
                    transition: all .3s ease-in-out;
                    &:hover, &:focus {
                        color: #fff;
                    }
                }
            }
        }
        p {
            font-family: ar-reg;
            font-size: 14px;
            color: #fff;
            margin: 0 0 12px 0;
            padding: 0;
        }
        .subscribe {
            width: 360px;
            height: 42px;
            position: relative;
            overflow: hidden;
            border-radius: 2px;
            input {
                outline: none;
                border: none;
                border-radius: 2px;
                background-color: #ffffff;
                width: 100%;
                height: 100%;
                padding: 0 20px 0 0;
                &::placeholder {
                    font-family: ar-reg;
                    font-size: 14px;
                    color: #b3b3b3;
                }
            }
            button {
                outline: none;
                border: none;
                width: 95px;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                background-color: #e21a34;
                font-family: ar-bold;
                font-size: 14px;
                color: #ffffff;
                transition: all .5s ease-in-out;
                &:hover {
                    width: 100px;
                    text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
                    box-shadow: 0 0 16px -6px #e21a34;
                }
            }
        }
        .icons {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 25px 0 30px 0;
            a {
                margin: 0 0 0 20px;
            }
            a:last-child {
                margin: 0;
            }
            i {
                font-size: 18px;
                color: #838383;
                transition: all .3s ease-in-out;
                &:hover {
                    color: #fff;
                }
            }
        }
        .our-phone {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            .phone-shadow {
                border-radius: 50%;
                background-color: rgba(60, 154, 104, 0.2);
                width: 60px;
                height: 60px;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all .5s ease-in-out;
                .phone-icon {
                    border-radius: 50%;
                    background-color: rgba(60, 154, 104, 0.302);
                    width: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .inner {
                        border-radius: 50%;
                        background-color: rgb(60, 154, 104);
                        width: 40px;
                        height: 40px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        i {
                            font-size: 18px;
                            color: rgb(255, 255, 255);
                            transform: rotateY(180deg);
                        }
                    }
                }
                &:hover {
                    transform: scale(1.02);
                }
            }
            .phone-number {
                font-family: ar-bold;
                font-size: 24px;
                color: #ffffff;
                margin: 6px 0 0 0;
                padding: 0;
            }
        }
        .logos {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 55px 0 0 0;
            img {
                margin: 0 0 0 30px;
            }
            img:last-child {
                margin: 0;
            }
        }
    }
    .sm-screen {
        display: none;
    }
    .copyright {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-top: 1px solid #332f30;
        height: 42px;
        .rights {
            span {
                font-family: ar-reg;
                font-size: 13px;
                color: #ababab;
            }
            img {
                display: inline-block;
                margin: 0 5px 0 0;
            }
        }
        .policy {
            ul {
                margin: 0;
                padding: 0;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                li {
                    margin: 0 0 0 15px;
                    a {
                        font-family: ar-reg;
                        font-size: 13px;
                        color: #ababab;
                        transition: all .3s ease-in-out;
                        &:hover, &:focus {
                            color: #fff;
                        }
                    }
                }
                li:last-child {
                    margin: 0;
                }
            }
        }
    }
}
/*------ footer end -------*/

.scroll-top {
    display: none;
    border: 1px solid #e21a34;
    border-radius: 50%;
    background-color: transparent;
    width: 40px;
    height: 40px;
    position: fixed;
    right: 3%;
    bottom: 10%;
    z-index: 999;
    font-size: 10px;
    color: #e21a34;
    cursor: pointer;
    transition: all .5s ease-in-out;
    i {
        line-height: 4;
    }
    &:hover {
        background-color: #e21a34;
        color: #f7f7f7;
    }
}


.fixed-menu {
    display: none;
}


